import React from 'react';
import classes from './MyPosts.module.css';
import Post from './Post/Post.jsx';
import { Field, reduxForm } from 'redux-form';
import { TextareaUI } from '../../common/FormsControls/FormsControls';
import { Button } from '@material-ui/core';


const MyPosts = (props) => {
    const postElements = props.postData.map(post => (
      <Post message={post.message} like={post.like} key={post.id} />
    ));
     
    const addNewPost = (values) => {
        props.addPost(values.AddNewPost);
        
    }

   return (
      <div className={classes.postsBlock}> 
          <h3>My posts</h3>
       <div>
          <MyPostReduxForm onSubmit={addNewPost} />
      </div>
         <div className={classes.posts}>{postElements}</div>
      </div>
    );
 }


const AddNewPostForm = (props) => {
  return <form className={classes.form} onSubmit={props.handleSubmit} autoComplete="off">
      <div>
        <Field component={TextareaUI} placeholder="Post message.." name="AddNewPost" />
      </div>
      <div className={classes.btn}>
        <Button size="small" type="submit" variant="contained" color="primary">
          Add Post
        </Button>
      </div>
    </form>;
} 

 const MyPostReduxForm = reduxForm({
   form: "AddPostForm"
 })(AddNewPostForm);

export default MyPosts;